import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { isIE } from 'react-device-detect'
import { Segment, Table, Icon, Image } from 'semantic-ui-react'
import { filterCountries, sortCountries } from '../reducers/countriesReducer'
import {
  getCountryNews,
  getCountryBbox,
  getCountryFeatures,
} from '../reducers/countryDataReducer'

const CountriesTable1 = () => {
  const dispatch = useDispatch()
  const state = useSelector((state) => state)
  const unit = useSelector((state) => state.unit.unit)
  const countriesFiltered = useSelector((state) => state.countries.filtered)

  const handleClick = (country) => {
    const location =
      country.capitalInfo.latlng === undefined
        ? country.latlng
        : country.capitalInfo.latlng
    dispatch(filterCountries(country))
    dispatch(getCountryBbox(country.name.common))
    dispatch(getCountryFeatures(location))
    if (country.region === 'Antarctic') {
      dispatch(getCountryNews('Antarctic'))
    } else {
      dispatch(getCountryNews(country.name.common))
    }
  }

  const tableHeaders = [
    /*  { fieldName: 'CCA3', id: 'cca3' }, */
    { fieldName: 'Location', id: 'location' },
    { fieldName: 'Flag', id: 'flag' },
    { fieldName: 'Name', id: 'name' },
    { fieldName: 'Capital', id: 'capital' },
    { fieldName: 'Region', id: 'region' },
    { fieldName: 'Subregion', id: 'subregion' },
    { fieldName: 'Population', id: 'population' },
    { fieldName: 'Area', id: 'area' },
  ]

  const sortIcons = (id) => {
    if (
      state.countries.sortConfig.direction === 'ascending' &&
      state.countries.sortConfig.key === id
    ) {
      return <Icon name="caret up" />
    } else if (
      state.countries.sortConfig.direction === 'descending' &&
      state.countries.sortConfig.key === id
    ) {
      return <Icon name="caret down" />
    }
  }

  const handleSort = (id) => {
    window.localStorage.setItem('sort key', id)
    dispatch(sortCountries(id))
  }

  return (
    <Segment attached="bottom">
      {
        countriesFiltered.length > 1 && (
          <>
            <Table sortable compact selectable unstackable size="small">
              <Table.Header
                id={
                  state.countries.filter.region.toLowerCase() === 'all'
                    ? 'one'
                    : 'two'
                }
              >
                <Table.Row>
                  <>
                    {tableHeaders.map(({ fieldName, id }) => (
                      <Table.HeaderCell key={id} onClick={() => handleSort(id)}>
                        {fieldName === 'Area'
                          ? unit === 'metric'
                            ? 'Area km²'
                            : 'Area mi²'
                          : fieldName}
                        {sortIcons(id)}
                      </Table.HeaderCell>
                    ))}
                  </>
                </Table.Row>
              </Table.Header>
              <Table.Body>
                <>
                  {countriesFiltered.map((item) => {
                    const areaConvert = Math.round(item.area / 2.59)
                    const location =
                      item.capitalInfo.latlng === undefined
                        ? item.latlng
                        : item.capitalInfo.latlng
                    return (
                      <Table.Row
                        data-test-id="table-row"
                        id="table-row"
                        key={item.cca3}
                        onClick={() => handleClick(item)}
                      >
                        <Table.Cell textAlign="center">
                          {JSON.stringify(location)}
                        </Table.Cell>
                        <Table.Cell>
                          <Image
                            srcSet={`${item.flags.svg} 100w`}
                            size="tiny"
                            src={isIE ? item.flags.png : item.flags.svg}
                            alt="country flag"
                            bordered
                            loading="lazy"
                          />
                        </Table.Cell>
                        <Table.Cell>{item.name.common}</Table.Cell>
                        <Table.Cell>
                          {item.capital === undefined
                            ? 'no data'
                            : item.capital}
                        </Table.Cell>
                        <Table.Cell>{item.region}</Table.Cell>
                        <Table.Cell>{item.subregion}</Table.Cell>
                        <Table.Cell>
                          {item.population.toLocaleString()}
                        </Table.Cell>
                        <Table.Cell>
                          {unit === 'imperial'
                            ? areaConvert.toLocaleString()
                            : item.area.toLocaleString()}
                        </Table.Cell>
                      </Table.Row>
                    )
                  })}
                </>
              </Table.Body>
            </Table>
          </>
        )
        //: null
      }
    </Segment>
  )
}

const CountriesTable = React.memo(CountriesTable1)

export default CountriesTable
